.item_detail {
  max-width: 800px;
  margin: 2rem auto;
  padding: 2rem;
  background-color: #fff;
  border-radius: 1rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  color: #333;
}
.item_detail img {
  width: 100%;
  height: auto;
  border-radius: 1rem;
  margin-bottom: 1.5rem;
  object-fit: cover;
}
.item_detail h2 {
  font-size: 2rem;
  color: #00796b;
  margin-bottom: 1rem;
  border-bottom: 2px solid #00796b;
  padding-bottom: 0.5rem;
}
.item_detail p {
  font-size: 1.1rem;
  margin: 0.8rem 0;
}
.item_detail p strong {
  color: #00796b;
  font-weight: 600;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Plus Jakarta Sans", sans-serif;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1024px;
  margin: 0 auto;
  position: relative;
  padding: 4px 16px;
  background: white;
}

.nav-logo img {
  width: 180px;
}

.nav-desktop-menu {
  display: flex;
  align-items: center;
  gap: 40px;
}
.nav-desktop-menu .nav-links {
  display: flex;
  gap: 32px;
  align-items: center;
  margin: 0;
}
.nav-desktop-menu .nav-links li {
  list-style: none;
}
.nav-desktop-menu .nav-links a {
  color: #000929;
  text-decoration: none;
}
.nav-desktop-menu .nav-links a:hover {
  color: #00796b;
  transition: 0.2s ease-in-out;
}

.nav-auth {
  display: flex;
  gap: 10px;
}
.nav-auth a {
  text-decoration: none;
}
.nav-auth-login {
  padding: 10px 30px;
  color: #00796b;
  transition: all 0.3s ease;
  font-weight: 600;
  border: 1px solid #00796b;
  border-radius: 8px;
}
.nav-auth-login:hover {
  border: 1px solid #000929;
}
.nav-auth-register {
  padding: 10px 20px;
  background: #00796b;
  color: white;
  transition: all 0.3s ease;
  font-weight: 600;
  border: 1px solid transparent;
  border-radius: 8px;
}
.nav-auth-register:hover {
  color: black;
  background: #e0def7;
}

.nav-toggle {
  display: none;
  font-size: 28px;
  cursor: pointer;
  z-index: 200;
}

.nav-mobile-menu {
  display: none;
  flex-direction: column;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background-color: white;
  padding: 24px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  z-index: 150;
  transition: all 0.3s ease;
}
.nav-mobile-menu.active {
  display: flex;
}
.nav-mobile-menu .nav-links-mobile {
  display: flex;
  flex-direction: column;
  gap: 14px;
  list-style-type: none;
  padding: 0 0 0 20px;
}
.nav-mobile-menu .nav-auth-mobile {
  display: flex;
  flex-direction: column;
  gap: 14px;
}
.nav-mobile-menu a {
  display: flex;
  justify-content: space-between;
  color: #000929;
  text-decoration: none;
  font-size: 18px;
  padding: 8px 0;
}
.nav-mobile-menu a:hover {
  color: #00796b;
}
.nav-mobile-menu .nav-auth-login {
  border: 2px solid #e0def7;
  border-radius: 8px;
  padding: 10px 20px;
  text-align: center;
}
.nav-mobile-menu .nav-auth-register {
  border: 2px solid #00796b;
  border-radius: 8px;
  padding: 10px 20px;
  background: #00796b;
  color: white;
  text-align: center;
}

@media (max-width: 768px) {
  .nav-links,
  .nav-auth {
    display: none !important;
  }
  .nav-toggle {
    display: block;
  }
}
.banner_wrapper {
  height: 100%;
}

#results {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  padding: 20px;
  max-width: 1024px;
  margin: 0 auto;
}

.listing {
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  cursor: pointer;
}
.listing img {
  width: 100%;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
}
.listing_content {
  padding: 16px;
}
.listing_content span {
  font-weight: 700;
  color: #00796b;
  font-size: 24px;
}
.listing_content_price {
  font-weight: 200;
}
.listing_content_properties {
  display: flex;
  align-items: center;
  gap: 8px;
}
.listing_content_properties p {
  margin: 0;
}
.listing_content_properties img {
  width: 18px !important;
}
.listing_content_properties i {
  color: #00796b;
}

.result_bg {
  background: #e5e5e5;
  height: 100%;
}

.result_wrapper {
  max-width: 1024px;
  margin: 0 auto;
  padding: 20px;
}

.hero_wrapper_content {
  max-width: 1024px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}
.hero_wrapper_content h1 {
  max-width: 544px;
  font-weight: 700;
}
.hero_wrapper_content p {
  max-width: 450px;
  font-size: 18px;
  font-weight: 400;
  margin: 0;
}
.hero_wrapper_content h1,
.hero_wrapper_content p {
  color: #000929;
}
.hero_wrapper_content span {
  color: #00796b;
  font-weight: 700;
  font-size: 32px;
}
.hero_wrapper_contentFlex {
  display: flex;
  margin: 16px 0 0 0;
}
.hero_wrapper_contentFlex div:first-of-type {
  border-right: 3px solid #d9d9d9;
  padding: 0 40px 0 0;
  margin: 0 40px 0 0;
}

.searchForm input[type=radio] {
  display: none;
}

.radio-label {
  cursor: pointer;
  position: relative;
  font-size: 18px;
  margin: 32px 0 0 0;
  background: white;
}
.radio-label:first-of-type {
  border-top-left-radius: 8px;
}
.radio-label:last-of-type {
  border-top-right-radius: 8px;
}

.radio_wrapper {
  display: flex;
}

.radio-label .radio-text {
  padding: 15px 32px;
  transition: border-bottom 0.2s ease;
}

.radio-label input[type=radio]:not(checked) + .radio-text {
  border-bottom: 2px solid #e5e5e5;
  font-weight: 400;
  color: #000929;
}

.radio-label input[type=radio]:checked + .radio-text {
  border-bottom: 2px solid #009688;
  font-weight: bold;
  color: #00796b;
}

.form-group-flex {
  display: flex;
  position: relative;
}
@media (max-width: 1024px) {
  .form-group-flex {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
}

.form-group {
  display: flex;
  flex-direction: column;
  background: white;
  padding: 25px 16px 25px 32px;
}
.form-group:first-of-type {
  border-bottom-left-radius: 8px;
}
@media (max-width: 1024px) {
  .form-group {
    width: 100%;
    padding: 10px 25px;
  }
}

.form-group-submit {
  padding: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: white;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  z-index: 100;
}
@media (max-width: 1024px) {
  .form-group-submit {
    width: 100%;
    justify-content: flex-start;
    align-items: center;
    padding: 10px 25px;
  }
}

.form-group label {
  margin-bottom: 5px;
  font-weight: 600;
  color: #333;
}

.form-input,
select {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 16px;
  transition: border-color 0.2s ease;
  max-width: 150px;
}

.form-input,
select:focus {
  border-color: #009688;
  outline: none;
}

.form-submit {
  padding: 12px 24px;
  background-color: #009688;
  color: white;
  font-weight: bold;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.form-submit:hover {
  background-color: #00796b;
}

.hero_bg {
  background: #e5e5e5;
}

.hero_wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 60px 20px;
  gap: 60px;
  align-items: center;
  min-height: 100vh;
  max-width: 1024px;
  margin: 0 auto;
}
@media (max-width: 1024px) {
  .hero_wrapper {
    grid-template-columns: 1fr;
    gap: 40px;
    padding: 40px 20px;
  }
}

.featured_properties {
  position: relative;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.featured_properties .property_showcase {
  position: relative;
  width: 100%;
  max-width: 400px;
  height: 500px;
  cursor: pointer !important;
}
.featured_properties .property_showcase .property_card {
  position: absolute;
  width: 100%;
  height: 450px;
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 12px;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  overflow: hidden;
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
  opacity: 0;
  transform: translateY(50px) scale(0.9);
}
.featured_properties .property_showcase .property_card.active {
  opacity: 1;
  transform: translateY(0) scale(1);
  z-index: 3;
}
.featured_properties .property_showcase .property_card.next {
  opacity: 0.7;
  transform: translateY(20px) scale(0.95);
  z-index: 2;
}
.featured_properties .property_showcase .property_card .property_image {
  width: 100%;
  height: 250px;
  background-size: cover;
  background-position: center;
  position: relative;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
}
.featured_properties .property_showcase .property_card .property_image .property_badge {
  position: absolute;
  top: 15px;
  left: 15px;
  background: #00796b;
  color: white;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
}
.featured_properties .property_showcase .property_card .property_info {
  padding: 16px;
}
.featured_properties .property_showcase .property_card .property_info .property_price {
  font-weight: 200;
  margin-bottom: 8px;
}
.featured_properties .property_showcase .property_card .property_info .property_price span {
  font-weight: 700;
  color: #00796b;
  font-size: 24px;
}
.featured_properties .property_showcase .property_card .property_info .property_title {
  font-size: 1.1rem;
  font-weight: 600;
  color: #1e293b;
  margin-bottom: 8px;
}
.featured_properties .property_showcase .property_card .property_info .property_location {
  color: #64748b;
  font-size: 0.9rem;
  margin-bottom: 15px;
}
.featured_properties .property_showcase .property_card .property_info .property_features {
  display: flex;
  align-items: center;
  gap: 8px;
}
.featured_properties .property_showcase .property_card .property_info .property_features p {
  margin: 0;
  font-size: 0.85rem;
  color: #64748b;
  display: flex;
  align-items: center;
  gap: 4px;
}
.featured_properties .property_showcase .property_card .property_info .property_features .icon {
  width: 18px !important;
}
.featured_properties .property_showcase .property_card .property_info .property_features i {
  color: #00796b;
}
.featured_properties .showcase_dots {
  position: absolute;
  bottom: -30px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  gap: 8px;
  width: 100%;
}
.featured_properties .showcase_dots .dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #d1d5db;
  cursor: pointer;
  transition: background 0.3s ease;
}
.featured_properties .showcase_dots .dot.active {
  background: #00796b;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}
.property_showcase {
  animation: float 6s ease-in-out infinite;
}

.property_arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #00796b;
  color: white;
  border: none;
  cursor: pointer;
  z-index: 10;
  user-select: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  transition: background-color 0.3s ease;
}
.property_arrow:hover {
  opacity: 80%;
}
@media (max-width: 1024px) {
  .property_arrow {
    bottom: 0;
  }
}

.prev_arrow {
  left: -50px;
}
@media (max-width: 1024px) {
  .prev_arrow {
    left: 10px;
  }
}

.next_arrow {
  right: -50px;
}
@media (max-width: 1024px) {
  .next_arrow {
    right: 10px;
  }
}